html,
body,
h1 {
  margin: 0;
  padding: 0; }

section {
  max-width: 700px;
  margin: 0 auto;
  padding-top: 70px; }
  section h1 {
    font-size: 2.5rem; }
  section p {
    font-size: 1.2rem; }

.topbar {
  position: fixed;
  left: 0;
  top: 0;
  background-color: darkolivegreen;
  height: 60px;
  width: 100%;
  font-size: 22px;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.2);
  opacity: 1;
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s; }
  .topbar span {
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    text-transform: capitalize;
    height: 100%; }

.hide {
  opacity: 0; }

.App {
  font-family: sans-serif;
  text-align: center;
}

